
import { defineAsyncComponent, defineComponent } from 'vue';

// interface ColorCardProps {
//   cIcon: string;
//   cTitle: string;
//   cDate: string;
//   cNum: number;
//   cCompare: string;
//   cType: boolean;
//   cBackground: string;
//   cColor: string;
// }

export default defineComponent({
  props: {
    cIcon: {
      type: String,
      require: true,
    },
    cTitle: {
      type: String,
      require: true,
    },
    cDate: {
      type: String,
      require: true,
    },
    cNum: {
      type: Number,
      require: true,
    },
    cCompare: {
      // 同比增长&下降
      type: String,
      require: true,
    },
    cType: {
      type: Boolean,
      require: true,
    },
    cBackground: {
      type: String,
      default: 'background1',
      require: true,
    },
    cColor: {
      type: String,
      default: '#357EF7',
      require: true,
    },
  },
  components: {
    aIcon: defineAsyncComponent(() => import('@/components/Icon.vue')),
  },
  setup(props) {
    return {
      props,
    };
  },
});
